// index.vue
<template>
  <div>
    <!-- echarts -->
    <div id="main" class="main_container" v-if="isShow"></div>
  </div>
</template>
<script>
import * as echarts from 'echarts/core'
import {
  TitleComponent,
  GridComponent,
  TooltipComponent
} from 'echarts/components'
import { BarChart, LineChart } from 'echarts/charts'
import { CanvasRenderer } from 'echarts/renderers'
echarts.use([
  TitleComponent,
  GridComponent,
  BarChart,
  CanvasRenderer,
  LineChart,
  TooltipComponent
])

export default {
  data () {
    return {
      isShow: false
    }
  },
  props: ['CardTableOne'],
  mounted () {
    this.initCharts()
  },
  watch: {
    CardTableOne (val) {
      this.$nextTick(() => {
        this.initCharts()
        // console.log(this.CardTableOne);
      })
    },
    immediate: true,
    deep: true
  },
  methods: {
    initCharts () {
      // echarts
      const myChart = echarts.init(document.getElementById('left'), null, {
        width: 410,
        height: 300
      })
      // 
      myChart.setOption({
        title: {
          text: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'axis',
          confine: 'true',
          formatter: ': {c0}<br/>{b0}'
        },
        grid: {
          left: '5%',
          right: '4%',
          bottom: '3%',
          top: '23%',
          containLabel: true,
          height: '60%'
        },
        xAxis: {
          boundaryGap: false,
          type: 'category',
          data: this.CardTableOne.xAxis,
          axisLine: {
            lineStyle: {
              color: 'rgba(154, 154, 154, 1)'
            }
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '',
            axisLine: {
              lineStyle: {
                color: 'rgba(154, 154, 154, 1)'
              }
            }
          },
          {
            type: 'value',
            interval: 500
          }
        ],
        // color: 'red',
        series: [
          {
            data: this.CardTableOne.series,
            type: 'line',
            smooth: true,
            areaStyle: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: 'rgba(236, 105, 99,0.5)' // 0% 
                  },
                  {
                    offset: 0.9,
                    color: 'white' // 100% 
                  }
                ],
                global: false //  false
              }
            },
            lineStyle: {
              color: 'red',
              width: 4
            },
            itemStyle: {
              color: 'red'
            },
            symbolSize: 7
          }
        ]
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.main_container {
  width: 300px;
  height: 300px;
  // overflow: hidden;
}
</style>
